<script>
const { ethers } = require("ethers");
import Footer from "@/components/Footer.vue";
import { onBeforeMount, onMounted } from "vue";
const abi = require("../assets/uniswap.json");
const unf = require("../assets/unf.json");
export default {
  name: "App",
  components: {
    Footer,
  },
  data: () => ({
    providers: [
      "https://mainnet.infura.io/v3/97c8bf358b9942a9853fab1ba93dc5b3",
      "https://mainnet.infura.io/v3/b203d49348c546c2b5614e5197788937",
      "https://mainnet.infura.io/v3/3258e142b54447a89b8c002ee7465a6d",
      "https://mainnet.infura.io/v3/7533c3c93ff146b1b791cae8af63c25b",
      "https://mainnet.infura.io/v3/533a7c4dec96470aa1682fe03f734cc7",
      "https://mainnet.infura.io/v3/a6a62579e0f94d3e86e77b28e1e0e52d",
      "https://mainnet.infura.io/v3/21f49e19bcbd40a58782763013eeafa6",
      "https://mainnet.infura.io/v3/407f253e1be4454e823175e370b765fd",
      "https://mainnet.infura.io/v3/abcdf7814da54da6a0bd864959202de8",
      "https://mainnet.infura.io/v3/5f9d174ed8ce4ce7b139c9a964207707",
      "https://mainnet.infura.io/v3/be18d2c65a2441c2b726064fb80f98b4",
    ],
    d: [],
    price: 0,
    supply: 0,
    target: 0,
    pairs: 0,
    h: 0,
    m: 0,
    s: 0,
    cir: 0,
    mcap: 0,
  }),
  async created() {
    try {
      let n = new Date();
      let seconds = Number(2555600400) - n.getTime() / 1000;
      seconds = parseInt(seconds);
      this.h = Math.floor((seconds % (3600 * 24)) / 3600);
      this.m = Math.floor((seconds % 3600) / 60);
      this.s = Math.floor(seconds % 60);

      setInterval(() => {
        seconds = seconds - 1;
        this.h = Math.floor((seconds % (3600 * 24)) / 3600);
        this.m = Math.floor((seconds % 3600) / 60);
        this.s = Math.floor(seconds % 60);
      }, 1000);

      fetch("rebase.json")
        .then((resp) => resp.json())
        .then((d) => {
          this.d = d.rebases;
          this.pairs = parseInt(d.pairs);
          this.target = d.target;
        });
      let rpc = this.providers[
        Math.floor(Math.random() * this.providers.length)
      ];
      const provider = new ethers.providers.JsonRpcProvider(rpc);
      let EthpC = new ethers.Contract(
        "0xb4e16d0168e52d35cacd2c6185b44281ec28c9dc",
        abi,
        provider
      );
      let reserve = await EthpC.getReserves.call();
      let r0 = parseFloat(reserve.reserve0);
      let r1 = parseFloat(reserve.reserve1);
      let p1 = (r0 / r1) * 10 ** 12;
      console.log(p1);

      let UnifpC = new ethers.Contract(
        "0xcfa6906e0c54e7d88bce9acfd3a8b599ba540dfa",
        abi,
        provider
      );
      let reserveU = await UnifpC.getReserves.call();
      let r0u = parseFloat(reserveU.reserve0);
      let r1u = parseFloat(reserveU.reserve1);
      let p2 = (r0u / r1u) * 10 ** 9;
      console.log(p2);

      this.price = parseInt((p1 * 1000) / p2) / 1000;

      const unifC = new ethers.Contract(
        "0x665a6b80208a1f8eccb1ecb6d0271dde910c9ef1",
        unf,
        provider
      );
      let supply = await unifC.totalSupply.call();
      let be = await unifC.balanceOf(
        "0x0000000000000000000000000000000000000000"
      );
      this.supply = parseInt((parseInt(supply) - parseInt(be)) / 10 ** 7) / 100;

      let locked = await unifC.balanceOf(
        "0xdbf72370021babafbceb05ab10f99ad275c6220a"
      );
      this.cir =
        parseInt(
          (parseInt(supply) - parseInt(be) - parseInt(locked)) / 10 ** 7
        ) / 100;

      this.mcap = parseInt(this.cir * this.price * 100) / 100;
    } catch (error) {
      console.log(error);
    }
  },
};
</script>